import React from "react";
import styled from "styled-components";
import Picture from "./picture.JPG";
import LocalPhoneIcon from "@mui/icons-material/LocalPhone";
import AlternateEmailIcon from "@mui/icons-material/AlternateEmail";
import "./summary.css";
import { useTranslation } from "react-i18next";

export const Wrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: 0%;
`;
const InfoLink = styled.a`
  margin-left: 5px;
  //color: #0288d1;
  &:hover {
    text-decoration: underline;
  }
`;
const About = styled.div`
  text-align: center;
  font-size: 16px;
  margin-top: 20px;
`;
const Image = styled.img`
  width: 170px;
  border-radius: 50%;
  border: 1 px solid var(--color-background-container-dark);
  margin-bottom: 20px;
`;
export function Summary() {
  const { t } = useTranslation();
  return (
    <Wrapper>
      <div className="personnal-info">
        <Image src={Picture} alt="" />
        <div className="name">MALIKA LAHFAOUI</div>
        <div className="credencials">
          <div className="tel-div">
            <LocalPhoneIcon
              sx={{ position: "relative", top: "3px", color: "#8b9eb7" }}
            />
            <InfoLink className="tel" href="tel:00212682575957">
              +212 6 82 57 59 57
            </InfoLink>
          </div>
          <div className="mail-div">
            <AlternateEmailIcon
              sx={{ position: "relative", top: "3px", color: "#8b9eb7" }}
            />{" "}
            <InfoLink className="mail" href="mailto:malika.lahfaoui@gmail.com">
              malika.lahfaoui@gmail.com
            </InfoLink>
          </div>
        </div>
      </div>
      <About>
        {/* <Title >{t("About_me")}</Title> */}
        <p>{t("about_me_resume")}</p>
      </About>
    </Wrapper>
  );
}
