import React from "react";
import styled from "styled-components";
import "./skills.css";
import { useTranslation } from "react-i18next";
import { Skill } from "./skills";

export const Wrapper = styled.div`
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: 0%;
`;

export function Skills() {
  const { t } = useTranslation();
  return (
    <Wrapper>
      <div className="others">
        <div className="title-column">{t("Technical_skills")}</div>
        <Skill />

        <div className="row">
          <div className="title-column">{t("other_Technical_skills")}</div>

          <div className="detail-column">
          <ul>
            <li>{t("Technical_skills_resume")}</li>
            <li>{t("project_life_cycle")}</li>
            <li>{t("desing_modeling")}</li>
            <li>{t("db")}</li>
          </ul>
          </div>
        </div>
      </div>
    </Wrapper>
  );
}
