import React from "react";
import styled from "styled-components";
import LinkedInIcon from "@mui/icons-material/LinkedIn";

const FooterWrapper = styled.div`
  position: absolute;
  bottom: 20px;
  right: 40px;
  @media (max-width:761px) {
    display:none;
  }
`;
const InfoLink=styled.a`
color: #0288d1;
`
export function Footer() {
  return (
    <FooterWrapper>
     
     <InfoLink
        href="https://www.linkedin.com/in/malika-lahfaoui/"
        target="_blank"
        rel="noreferrer"
      >
       <LinkedInIcon color="info" fontSize="small" sx={{position:'relative',top:'3px'}} ></LinkedInIcon> LinkedIn
      </InfoLink>
    </FooterWrapper>
  );
}
