import ReadMoreAndLess from 'react-read-more-less';
import { useTranslation } from "react-i18next";

export function ReadMoreAndLessText () {
  const {t}  = useTranslation();
        return (
            <ReadMoreAndLess
       
                charLimit={100}
                readMoreText={t("read_more")}
                readLessText={t("read_less")}
            >

          {t("read_more_1")}
         
          
         
        
            </ReadMoreAndLess>
        );
    
}