import React from "react";
import styled from "styled-components";
import { ReadMoreAndLessText } from "./readmoreandless";
import "./work.css";
import "react-perfect-scrollbar/dist/css/styles.css";
import PerfectScrollbar from "react-perfect-scrollbar";
import Title from "../Title";
import { useTranslation } from "react-i18next";

export const Wrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  //height: 0%;
  height: calc(100vh - 160px);
  max-height: 500px;
  padding-left: 30px;
  @media (max-width: 761px) {
    height: inherit;
    max-height: inherit;
    padding-left: 10px;
  }
`;

export function Work() {
  const {t}  = useTranslation();
  return (
    <Wrapper>
      <PerfectScrollbar>
        <div className="work">
          <Title>{t("job_title")}</Title>
          <p>
            <h6>{t("hps_work")}</h6>
            <span>{t("resposabilities")}</span>
            <ReadMoreAndLessText sx={{ color: "#ffffff" }} />
            <span>{t("Missions")} </span>
           <ul>
             <li>{t("mission_1")}</li>
            <li>{t("mission_2")}</li>
           </ul>
             <br />
            
            <span> {t("Technical_environment")} </span> {t("tech_env_1")}
          </p>
        </div>
        <div>
          <Title> {t("Front-end_developer")}</Title>
          <h6>{t("novelis_work")}</h6>
          {t("novelis_work_resume")}
          <br />
          <span>{t("Projects")}</span> {t("Projects_name")} <br />
          <span>{t("Technical_environment")}</span> {t("tech_env_2")}
        </div>
      </PerfectScrollbar>
    </Wrapper>
  );
}
