import React, { useState } from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";
import { getPages } from "../data";
import { useLocation } from "react-router-dom";
import Hamburger from "hamburger-react";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import "./Navigation.css";
import { useTranslation } from "react-i18next";
import LanguageSelect from "../languageSelect";

const mobileView = "1061px";

const Wrapper = styled.div`
  padding: 32px;
  width: 100%;
  z-index: 3;
  text-align: center;
  position: absolute;

  @media (max-width: 761px) {
padding:10px 20px;
background-color:#24253c;
  }
`;

const Nav = styled.nav`
  width: 100%;
  @media (max-width: ${mobileView}) {
    display: ${({ isOpen }) => (isOpen ? "block" : "none")};
    padding-top:50px;
  }
`;

const NavLink = styled.a`
  border-radius: 8px;
  font-weight: 500;
  padding: 12px 24px;
  text-decoration: none;
  transition: background-color 200ms ease;
  &:hover {
    background-color: rgba(47, 66, 80, 0.2);
  }
  @media (max-width: ${mobileView}) {
    display: block;
    border-bottom: 1px solid #393939;
    border-radius: 0;
    padding: 20px 0;
  }
`;

const StyledHamburger = styled.div`
  display: none;
  float: right;
  @media (max-width: ${mobileView}) {
    display: block;
  }
`;

const Overlay = styled.div`
  display: none;
  z-index: -1;
  @media (max-width: ${mobileView}) {
    display: block;
    background-color: rgba(0, 0, 0, 0.8);
    position: absolute;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
  }
`;
const InfoLink=styled.a`
color: #0288d1;
`;

export function Navigation(props) {
  const { pathname } = useLocation();
  const [isOpen, setOpen] = useState(false);
  const {t}=useTranslation();
 
  return (
    <Wrapper {...props}>
      {isOpen && <Overlay onClick={() => setOpen(false)} />}

      <StyledHamburger>
        <Hamburger toggled={isOpen} toggle={setOpen} />
      </StyledHamburger>
      <LanguageSelect />
      <Nav isOpen={isOpen}>
        {getPages(t).map((page, index) => (
          <NavLink
            as={Link}
            key={index}
            to={page.path}
            onClick={() => setOpen(false)}
            className={page.path === pathname ? "menu-active" : ""}
          >
            {page.title}
          </NavLink>
          
        ))}
        <NavLink className="hide-mobile">
              <InfoLink
        href="https://www.linkedin.com/in/malika-lahfaoui/"
        target="_blank"
        rel="noreferrer"
      >
       <LinkedInIcon color="info" fontSize="small" sx={{position:'relative',top:'3px'}} ></LinkedInIcon> LinkedIn
      </InfoLink>
      </NavLink>
      </Nav>

    </Wrapper>
  );
}
