import React from "react";
import styled from "styled-components";
import "./others.css";
import { useTranslation } from "react-i18next";
//import { Skills } from "./skills";
import PerfectScrollbar from "react-perfect-scrollbar";
import { Grid } from "@mui/material";
import VolunteerActivismIcon from "@mui/icons-material/VolunteerActivism";
import LanguageIcon from "@mui/icons-material/Language";
import GroupsIcon from "@mui/icons-material/Groups";
import AppRegistrationIcon from "@mui/icons-material/AppRegistration";
import Chip from "@mui/material/Chip";
import StyledSubtitles from "../Title_sub";

export const Wrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: calc(100vh - 160px);
  margin-left: 30px;
  @media (max-width: 761px) {
    width: 100%;
    margin-left: 0;
    height: inherit;
  }
`;

const ListItem = styled.div``;
const Item = styled(Grid)`
  @media (max-width: 761px) {
    padding-bottom: 30px;
    text-align: center;
  }
`;

export function Others() {
  const { t } = useTranslation();
  return (
    <Wrapper>
      <PerfectScrollbar>
        <div className="others">
          <Grid
            container
            rowSpacing={3}
            columnSpacing={{ xs: 0, sm: 0, md: 3 }}
          >
            <Item item xs={12} md={6}>
              <div className="FolderIcon group">
                <GroupsIcon
                  fontSize="large"
                  sx={{ position: "relative", top: "25%" }}
                />
              </div>
              <StyledSubtitles>{t("Social_skills")}</StyledSubtitles>

              <ListItem>
                {t("Social_skills_resume")
                  .split(",")
                  .map((item) => (
                    <Chip
                      label={item}
                      sx={{
                        margin: "5px",
                        color: "#e2e2e5",
                        borderRadius: "5px",
                      }}
                      variant="outlined"
                    />
                  ))}
              </ListItem>
            </Item>

            <Item item xs={12} md={6}>
              <div>
                <div className="FolderIcon language">
                  <LanguageIcon
                    fontSize="large"
                    sx={{ position: "relative", top: "25%" }}
                  />
                </div>

                <StyledSubtitles>{t("Languages")}</StyledSubtitles>

                <ListItem>
                  {t("Languages_resume")
                    .split(",")
                    .map((item) => (
                      <Chip
                        label={item}
                        sx={{
                          margin: "5px",
                          color: "#e2e2e5",
                          borderRadius: "5px",
                        }}
                        variant="outlined"
                      />
                    ))}
                </ListItem>
              </div>
            </Item>
            <Item item xs={12} md={6}>
              <div sx={{ maxWidth: "50%" }}>
                <div className="FolderIcon voluneer">
                  <VolunteerActivismIcon
                    fontSize="large"
                    sx={{ position: "relative", top: "25%" }}
                  />
                </div>
                <StyledSubtitles>{t("Interests_Volunteering")}</StyledSubtitles>

                <ListItem>
                  {t("Interests_Volunteering_resume")
                    .split(",")
                    .map((item) => (
                      <Chip
                        label={item}
                        sx={{
                          margin: "5px",
                          color: "#e2e2e5",
                          borderRadius: "5px",
                        }}
                        variant="outlined"
                      />
                    ))}
                </ListItem>
              </div>
            </Item>
            <Item item xs={12} md={6}>
              <div className="FolderIcon registration">
                <AppRegistrationIcon
                  fontSize="large"
                  sx={{ position: "relative", top: "25%" }}
                />
              </div>
              <StyledSubtitles>{t("Organizational_skills")}</StyledSubtitles>

              <ListItem>
                {t("Organizational_skills_resume")
                  .split(",")
                  .map((item) => (
                    <Chip
                      label={item}
                      sx={{
                        margin: "5px",
                        color: "#e2e2e5",
                        borderRadius: "5px",
                      }}
                      variant="outlined"
                    />
                  ))}
              </ListItem>
            </Item>
          </Grid>
        </div>
      </PerfectScrollbar>
    </Wrapper>
  );
}
