import React from "react";
import styled from "styled-components";
import htmlImg from "./img/html-5.png";
import cssImg from "./img/css-3.png";
import angularImg from "./img/angular.png";
import githubImg from "./img/github.png";
import jsImg from "./img/js.png";
import reactImg from "./img/react.png";
import sassImg from "./img/sass.png";
import typescriptImg from "./img/typescript.png";

import { Grid } from "@mui/material";
import "./skills.css";

const Container = styled.div`
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  margin-top: 20px;
  margin-bottom: 20px;
`;
const IMG = styled.img`
  max-width: 80px;
  width: 100%;
  height: 80px;
  margin-bottom: 15px;
  transition: 0.3s;
`;

const SkillDiv = styled.div`
  background: #7992a0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 15px;
  border-radius: 5px;
  transition: 0.3s;
  &:hover {
    & img {
      transform: scale(1.1);
      transform: rotate(10deg);
    }
    transform: scale(1.1);
  }
`;

const LanguageTitle = styled.h3`
  color: #000;
  font-weight: 700;
  text-transform: capitalize;
  font-size: 20px;
  margin: 0;
`;

const data = [
  { img: htmlImg, label: "Html" },
  { img: cssImg, label: "Css" },
  { img: angularImg, label: "Angular" },
  { img: reactImg, label: "React" },
  { img: jsImg, label: "Javascript" },
  { img: sassImg, label: "Sass" },
  { img: typescriptImg, label: "Typescript" },
  { img: githubImg, label: "Github" },
];
export function Skill() {
  return (
    <Container>
      <Grid container spacing={2}>
        {data.map(({ label, img }) => (
          <Grid item xs={6} md={3}>
            <SkillDiv>
              <IMG src={img} alt="" />
              <LanguageTitle>{label}</LanguageTitle>
            </SkillDiv>
          </Grid>
        ))}
      </Grid>
    </Container>
  );
}
