import React from "react";
import styled from "styled-components";

const StyledTitle = styled.h2`
  color: #8B9EB7;
  font-size: xx-large;
  &:after {
    content: "";
    display: block;
    */width: 50%;
    padding-top: 11px;
    margin-bottom: 15px;
    border-bottom: 1px solid #8B9EB7;
    width: 23%;
  }
`;
export default function Title({ children }) {
  return <StyledTitle>{children}</StyledTitle>;
}
