import { Summary } from "../components/Summary/index";
import work from "./../img/work.png";
import education from "./../img/education.png";
import skills from "./../img/skills.png";
import others from "./../img/others.png";
import { Education } from "../components/Education/index";
import { Work } from "../components/Work/index";
import { Skills } from "../components/Skills/index";
import { Others } from "../components/Others/index";


export const getPages = (t)=>[
    {
      path: "/",
      title: t("personal_summary"),
      color: "#03a9f4",
      content: <Summary />,

    },
    {
      path: "/work",
      title: t("Work_experiences"),
      color: "#ff9c07",
      content:<Work/>,
      background:`url(${work})`
    },
    {
      path: "/education",
      title: t("Education_and_training"),
      color: "#F44336",
      content:<Education/>,
      background:`url(${education})`
    },
    {
      path: "/skills",
      title: t("Personal_skills"),
      color: "#673AB7",
      content:<Skills/>,
      background:`url(${skills})`
    },
    {
      path: "/others",
      title: t("others"),
      color: "#3F51B5",
      content:<Others/>,
      background:`url(${others})`
    }

  ];
  